import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsAccountsListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component';
import { IOptionsPsComplexExchnageExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-exchange/ps-complex-exchange.component.interfaces';
import { IOptionsPsLookupDebitCardsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-debit-cards/ps-lookup-debit-cards.component.interfaces';
import { IOptionsPsLookupOwnAccountsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-accounts/ps-lookup-own-accounts.component.interfaces';
import { IOptionsPsLookupPrepaidCardsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-prepaid-cards/ps-lookup-prepaid-cards.component.interface';
import { IOptionsPsLovRechargeMethodsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-lov/ps-lov-recharge-methods/ps-lov-recharge-methods.interface';
import { IValidateCurrencyCombination, RechargeCardVO } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { OmniBasePage } from 'src/app/pages/omni-base/omni-base.page';

@Component({
  selector: 'app-prepaid-card-recharge',
  templateUrl: './prepaid-card-recharge.page.html',
  styleUrls: ['./prepaid-card-recharge.page.scss'],
})
export class PrepaidCardRechargePage extends OmniBasePage implements OnInit {

  @ViewChild('toCardRef') toCardRef;
  @ViewChild('fromCardRef') fromCardRef;
  formGroup: FormGroup = new FormGroup({});
  rechargeCardVO: RechargeCardVO = {};
  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'prepaid_card_stepper',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: [
      'prepaid_card_req_step1',
    ],
    group: this.formGroup,
    submitOptions: {
      extraParams: {},
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.creditCardRequest,
      group: this.formGroup,
    },
    requestObject: this.rechargeCardVO,
  };

  panelOptions1Step1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'prepaid_card_recharge_details_key',
    expanded: true,
  };

  fromCardOptions: IOptionsPsLookupDebitCardsExposed = {
    labelKey: 'from_card_key',
    placeHolder: 'select_from_card_key',
    group: this.formGroup,
    fcName: 'fromCard',
    requestObject: this.rechargeCardVO,
  };
  toCardOptions: IOptionsPsLookupPrepaidCardsExposed = {
    labelKey: 'to_card_key',
    placeHolder: 'select_to_card_key',
    group: this.formGroup,
    fcName: 'toCard',
    requestObject: this.rechargeCardVO,
  };

  public complexExchangeOptions: IOptionsPsComplexExchnageExposed = {
    editableMode: false,
    showToCurrencyOptions: true,
    fromAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'currency_key',
        labelKey: 'currency_key',
        fcName: 'fromCurrency',
        group: this.formGroup
      },
      amountOptions: {
        labelKey: 'amount_key',
        placeHolder: 'enter_amount_key',
        fcName: 'transactionAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    toAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'exchange_currency_key',
        labelKey: 'exchange_currency_key',
        fcName: 'currency',
        group: this.formGroup
      },
      amountOptions: {
        labelKey: 'exchange_amount_key',
        placeHolder: 'enter_amount_key',
        fcName: 'toAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    requestObject: this.rechargeCardVO,
    group: this.formGroup
  };

  public fromAccountOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'from_account_key',
    placeHolder: 'select_from_account_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'fromAccount',
    accountType: 'G',
    fromTo: 'from',
    requestObject: this.rechargeCardVO
  };

  rechargeMethodsOptions: IOptionsPsLovRechargeMethodsExposed = {
    group: this.formGroup,
    fcName: 'rechargeMethods',
  };


  constructor(private navService: PsNavigatorService, public omniPull: OmniPullService) {
    super();
  }

  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    this.commonProv.copyObject(this.rechargeCardVO, this.navService.getAllParams() ? this.navService.getAllParams().ScreenVO ? this.navService.getAllParams().ScreenVO : this.navService.getAllParams() : {})

  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName,
    this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName], 1);

    if (this.rechargeCardVO && this.rechargeCardVO[this.toCardOptions.fcName]) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.toCardOptions.fcName], 1)
    }
    const rechargeMethodCust = this.commonProv.getElementValidations(this.rechargeMethodsOptions.fcName);
    if (rechargeMethodCust.DEFAULT_VALUE && rechargeMethodCust.IS_VISIBLE == 0) {
      const rechargedMethod = {
        itemValue: JSON.parse(rechargeMethodCust.DEFAULT_VALUE).value
      }
      this.onRechargeMethodsChange(rechargedMethod);
    }

  }


  public onFromAccountChange(fromAccount) {
    this.complexExchangeOptions.fromAmountOptions.currency = fromAccount.currencyBriefNameEnglish;
    this.complexExchangeOptions.fromAmountOptions.currencyCode = fromAccount.currency;
    setTimeout(() => {
      this.stepperOptions.requestObject[this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName] = fromAccount.currency;
    }, 1);
  }

  //added by Marina TP:#1542494 to update exchange component from currency
  onFromCardChange(fromCard) {
    this.validateCurrencyCombination(fromCard);
    this.complexExchangeOptions.fromAmountOptions.currency = fromCard.currencyDesc;
    this.complexExchangeOptions.fromAmountOptions.currencyCode = fromCard.currency;
    setTimeout(() => {
      this.stepperOptions.requestObject[this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName] = fromCard.currency;
    }, 1);
  }

  //added by Marina TP:#1542494 to update exchange component to currency
  onToCardChange(toCard) {
    this.validateCurrencyCombination(toCard);
    this.complexExchangeOptions.toAmountOptions.currency = toCard.currencyDesc;
    this.complexExchangeOptions.toAmountOptions.currencyCode = toCard.currency;
    setTimeout(() => {
      this.stepperOptions.requestObject[this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName] = toCard.currency;
    }, 1);
  }
  //added by Marina TP:#1542494 to hide and show from account and from card fields based on recharge method chosen
  onRechargeMethodsChange(rechargeMethod) {
    if (rechargeMethod.itemValue == 'A') {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.fromCardOptions.fcName], 0);
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.fromAccountOptions.fcName], 1);
    }
    else {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.fromAccountOptions.fcName], 0);
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.fromCardOptions.fcName], 1);
    }
  }

  // Added by Omar Talaat To fix #IDB230103
  private validateCurrencyCombination(selectedAccount) {
    if (selectedAccount[ConstantCommon.VALIDATE_CURRENCY_COMBINATION]) {
      if (this.rechargeCardVO[this.fromCardOptions.fcName] && this.rechargeCardVO[this.toCardOptions.fcName]) {
        let validationRequest: IValidateCurrencyCombination = {
          fromCurrency: +this.rechargeCardVO[this.fromCardOptions.fcName].currency,
          toCurrency: +this.rechargeCardVO[this.toCardOptions.fcName].currency,
          listOfAccounts: [this.fromCardRef, this.toCardRef],
          listOfAffectedFields: [this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName, this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName],
          screenVO: this.rechargeCardVO
        };
        this.omniPull.validateCurrencyCombination(validationRequest);
      }
    }

  }

  updateScreenVo(resp) {
    this.rechargeCardVO.submitType = '-1'; // Added by: MFawzy - tp: #1542492 - as requested per Doris to get the correct mappingId with the submit service
  }

}
